import utils from '../../utils';

export const base = theme => ({
  expandedWidth: utils.getWidthByUpCount(theme, 4),
  expandedHeight:
    utils.getDimensions(theme, { ratioX: 16, ratioY: 9, upCount: 4 }).h +
    theme.spacer.xxxl * 7 +
    theme.spacer.lg +
    theme.spacer.xxs,
  metadata: { descriptionTextStyle: { maxLines: 3 } },
  marqueeOnFocus: true
});
